/*RESET ==============================*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

li, input{
  display: block;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid black;
  -webkit-text-fill-color: black;
  -webkit-box-shadow: 0 0 0px 1000px white inset;
}

/*MAIN ==========================================================*/

.container{
  font-family: 'Poppins', sans-serif;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  height: 100vh;
  color:#0e002f;
}
a,a:hover,a:active,a:visited{
  text-decoration: none;
}

h1{font-size: 2.5em;margin: 2%; font-family: 'K2D', sans-serif; font-weight: 700;font-style: italic;}
h2{font-size: 2em;margin: 2%;}
h3{font-size: 1.2em;margin: 2%;}
h4{font-size: 1em;margin: 2%;}
h5{font-size: 0.9em;margin: 2%;}
p{font-size: 0.8em;margin: 2%;}
label{font-size: 0.8em;}
input{font-size: 1em; font-family: 'Poppins', sans-serif;text-align: center;}


form{
  width: 80%;
  padding: 3% 5% 13% 5%;
  margin: 0 auto;
  border: 5px solid #eee;
  border-radius:20px;
  background: white;
  box-shadow: 0px 0px 35px -51px rgba(0,0,0,0.38);
  transition: all 300ms;
  position: relative;
  z-index: 2;
  box-shadow: inset 0px 0px 5px 0px rgba(0,0,0,0.5);
}

input[type="text"],input[type="number"],input[type="password"],input[type="email"]{
  width: 100%;
  height: 45px;
  border-radius: 50px;
  margin: 5px 0;
  border: 1px solid #d6d5d5;
  outline: none;
}

