.radio-wrapper{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  text-align: center;
  
}

.radio-checked{
  width:45%;
  display: block;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.radio-checked input{
  position: relative;
  opacity: 0;
  cursor: pointer;
  display: none;
}

.radio-checked input:checked ~ .checkmark-business {
  border:5px solid #e20088;
  background:#ffe1e1;
  -webkit-transform: scale(1.1) rotate(2deg);
          transform: scale(1.1) rotate(2deg);
  box-shadow: inset 0px 0px 22px 0px rgb(255, 100, 38);
  z-index: 3;
}
.radio-checked input:checked ~ .checkmark-customer {
  border:5px solid #ff6126;
  background:#fff5e3;
  -webkit-transform: scale(1.1) rotate(-2deg);
          transform: scale(1.1) rotate(-2deg);
  box-shadow: inset 0px 0px 22px 0px rgb(255, 100, 38);
  z-index: 3;
  
}
.checkmark-customer, .checkmark-business{
  display: block;
  position: relative;
  padding: 15% 0;
  width: 100%;
  border: 4px solid #eee;
  border-radius: 10px;
  margin: 20px 0;
  transition: all 500ms;
  box-shadow: inset 0px 0px 5px 0px rgba(0,0,0,0.5);
}

